$text-muted: $gray;
$NotoSansPath: "~notosans-fontface/fonts";
$NotoSansVersion: "1.0.1";
$modal-dialog-margin:         38px !default;
$modal-dialog-sm-up-margin-y: 38px !default;


$white:				#fff;
$almost-white:				#fcfcfc;
$font-family:				  		Tahoma,Arial,sans-serif;
$font:						  		normal 12px/150% $font-family;
$font-custom:                 		'Vollkorn', sans-serif;
$font-icon:                   		"fontello";

//
$theme-color:                   #00a161;
$highlight-color:					#E54D26;
$gray-color:						#999999;

//-------- BORDERS
$base-border-color:					#f2f2f2; //
$main_border:						1px solid $base-border-color;
$dark-border-color:					#444;//
$light-border-color:				#fff;
$gray-border-color:            		#d9d9d9;//
$title-border-color:            	#d9d9d9;
$input-border-focus:				#666;

//--------- BACKGROUNDS
$base-box-bg:                       #fcfcfc;//
$table-background:                  $base-box-bg;
$dark-background:                   #444;//
$light-background:                  #fff;//
$gray-background:                #E9E9E9;
$base-background:                	#f2f2f2; //

//--------- COLORS
$base-body-color:                   #777;
$base-text-color:                   #777; //
$dark-text-color:                   #444; //
$base-heading-color:				#444; //
$base-title-color:					$base-text-color;
$light-text-color:                  #fff;//
$link-color:						#444444; //
$link-hover-color:                  #00a161; //
$price-color:						#444;
$product-old-price-color:			#999;
$product-special-price-color:		#f13340;

//---------- Buttons
$button-text-color:					$link-color;
$button-hover-color:				#fff;
$button-hover-bg-color:				$link-color;

//--------- Indents
$base-box-padding:                  14px 18px 13px;
$base-box-indent:                  	0 0 30px 0;

//st override
$pagination-border-width: 0;
$pagination-bg: transparent;
$pagination-hover-bg: $base-background;
$pagination-active-bg: transparent;
$pagination-disabled-bg: transparent;
$pagination-active-color: $gray-color;
$pagination-disabled-color: $gray-color;
$pagination-color: $link-color;
$pagination-hover-color: $link-color;
$pagination-padding-x:             9px;
$pagination-padding-y:             6px;
//

// Extra small screen / phone
$st-screen-sm:                  480px !default;
$st-screen-phone:               $st-screen-sm !default;

// Small screen / tablet
$st-screen-md:                  768px !default;
$st-screen-tablet:              $st-screen-md !default;

// Medium screen / desktop
$st-screen-lg:                  992px !default;
$st-screen-desktop:             $st-screen-lg !default;

// Large screen / wide desktop
$st-screen-xl:                  1200px !default;
$st-screen-xl-desktop:          $st-screen-xl !default;

// Large screen / wide desktop
$st-screen-xxl:                  1440px !default;
$st-screen-xxl-desktop:          $st-screen-xxl !default;

// full screen
$st-screen-fw:                  1600px !default;
$st-screen-fw-desktop:          $st-screen-fw !default;

// So media queries don't overlap when required, provide a maximum
$st-screen-sm-max:              ($st-screen-md - 1) !default;
$st-screen-md-max:              ($st-screen-lg - 1) !default;
$st-screen-lg-max:              ($st-screen-xl - 1) !default;
$st-screen-xl-max:              ($st-screen-xxl - 1) !default;
$st-screen-xxl-max:              ($st-screen-fw - 1) !default;

$st-grid-gutter-width:         30px !default;

// Extra small screen / phone
$st-container-phone:            516px !default;

// Small screen / tablet
$st-container-tablet:            768px !default;

// Medium screen / desktop
$st-container-desktop:           992px !default;

// Large screen / wide desktop
$st-container-lg-desktop:        1200px !default;

// Extra large screen
$st-container-xl-desktop:        1440px !default;

// Extra large screen
$st-container-xxl-desktop:        1600px !default;

$output-bourbon-deprecation-warnings: false;


